@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #f5f5f5;
  --color-bg-variant: #cacaca;
  --color-primary: #000102;
  --color-primary-variant: rgba(54, 62, 68, 0.4);
  --color-black: black;
  --color-dark: rgba(58, 53, 53, 0.6);
  --transition: all 0.4s ease;
  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: 'Poppins', sans-serif;
  color: var(--color-white);
  line-height: 1.7;
  background-image: url(/react-portfolio-website/static/media/bg.b628a6e9.jpg);
}

.container {
  width: 75%;
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
}

section>h2,
section>h5 {
  text-align: center;
  color: rgba(58, 53, 53, 0.6);
  color: var(--color-dark);
}

section>h2 {
  color: #000102;
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-dark {
  color: rgba(58, 53, 53, 0.6);
  color: var(--color-dark);
  transition: all 0.4s ease;
  transition: var(--transition);
}

a {
  color: #000102;
  color: var(--color-primary);
  transition: all 0.4s ease;
  transition: var(--transition);
}

a:hover {
  color: rgba(58, 53, 53, 0.6);
  color: var(--color-dark);
}

.btn {
  width: -webkit-max-content;
  width: max-content;
  display: inline-block;
  color: #000102;
  color: var(--color-primary);
  background: #f5f5f5;
  background: var(--color-bg);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid #000102;
  border: 1px solid var(--color-primary);
  transition: all 0.4s ease;
  transition: var(--transition);
}

.btn:hover {
  background: #cacaca;
  background: var(--color-bg-variant);
  color: black;
  color: var(--color-black);
  border-color: black;
  border-color: var(--color-black);
}

.btn-primary {
  background: #000102;
  background: var(--color-primary);
  color: #f5f5f5;
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

@media screen and (max-width: 1024px) {
  .container {
    width: 86%;
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem;
  }
}

@media screen and (max-width: 600px) {
  .container {
    width: 90%;
    width: var(--container-width-sm);
  }

  section>h2 {
    margin-bottom: 2rem;
  }
}
header {
    height: 100vh;
    padding-top: 4rem;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.cta {
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

.socials {
    font-size: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: fixed;
    left: 2rem;
    bottom: 3rem;
}

.socials::after {
    content: '';
    width: 2px;
    height: 2rem;
    background-color: var(--color-primary);
}

.profile {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

.scroll__down {
    position: fixed;
    right: -0.6rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .socials,
    .scroll__down {
        display: none;
    }
}
nav {
    background: rgba(0, 0, 0, 0.5);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(12px);
            backdrop-filter: blur(12px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    font-size: 1.1rem;
    color: aliceblue;
}

nav a:hover {
    background: rgb(94, 94, 94);
    color: white;
}

nav a.active {
    background: var(--color-bg);
    color: var(--color-black);
}
.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
}

.about__me {
    width: 120%;
    aspect-ratio: 2/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
}

.about__me img {
    border-radius: 2rem;
    overflow: hidden;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: var(--transition);
}

.about__me img:hover {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

.about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover {
    background: var(--color-dark);
    color: aliceblue;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon {
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
}

.about__content p {
    background: var(--color-bg-variant);
    border-radius: 1rem;
    padding: 1rem;
    margin: 2rem 0 2.6rem;
    color: var(--color-black);
    transition: var(--transition);
}

.about__content p:hover {
    background: var(--color-dark);
    color: aliceblue;
    border-color: var(--color-primary-variant);
    cursor: default;
}

@media screen and (max-width: 1024px) {
    .about__container {
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }

    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
    }
}

@media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p {
        margin: 1.5rem 0;
    }
}
.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 3rem;
    gap: 3rem;
}

.experience__container>div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container>div:hover {
    color: white;
    background: var(--color-dark);
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experience__container>div h3 {
    font-size: 1.2rem;
    border: 1px solid transparent;
    border-radius: 2rem;
    background: var(--color-dark);
    text-align: center;
    margin-bottom: 2rem;
    color: white;
}

.experience__container>div h4 {
    font-size: 1.2rem;
    text-align: center;
    margin-bottom: 2rem;
}

.experience__container>div h5 {
    text-align: center;
    font-size: 1rem;
    margin-bottom: 2rem;
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience__details {
    border: 1px solid;
    padding: 2rem;
    border-radius: 2rem;
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

@media screen and (max-width: 1024px) {
    .experience__container {
        grid-template-columns: 1fr;
    }

    .experience__container>div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .experience__content {
        padding: 1rem;
    }
}

@media screen and (max-width: 600px) {
    .experience__container {
        grid-gap: 1rem;
        gap: 1rem;
    }

    .experience__container>div {
        width: 100%;
        padding: 2rem 1rem;
    }
}
.project__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.project__item {
    background: var(--color-bg-variant);
    padding: 1.5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.project__item:hover {
    border: 1px solid var(--color-primary);
    background: var(--color-primary-variant);
    color: white;
}

.project__item-image {
    width: 200px;
    height: 100px;
    font-size: 2rem;
    border-radius: 1.5rem;
    overflow: hidden;
}

.project__item-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.project__item h3 {
    margin: 1.2rem 0 2rem;
}

@media screen and (max-width: 1024px) {
    .project__container {
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }
}

@media screen and (max-width: 600px) {
    .project__container {
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
}
.skills__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 3rem;
    gap: 3rem;
}

.skills__container>div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.skills__container>div:hover {
    color: white;
    background: var(--color-dark);
    border-color: var(--color-primary-variant);
    cursor: default;
}

.skills__container>div h3 {
    text-align: center;
    border: 1px solid transparent;
    border-radius: 2rem;
    background: var(--color-dark);
    color: white;
    margin-bottom: 2rem;
}

.skills__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.skills__details {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.skills__details-icon {
    margin-top: 6px;
    flex-shrink: 0;
}

@media screen and (max-width: 1024px) {
    .skills__container {
        grid-template-columns: 1fr;
    }

    .skills__container>div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .skills__content {
        padding: 1rem;
    }
}

@media screen and (max-width: 600px) {
    .skills__container {
        grid-gap: 1rem;
        gap: 1rem;
    }

    .skills__container>div {
        width: 100%;
        padding: 2rem 1rem;
    }
}
.container.contact__container {
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}

.contact__options {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

.contact__option {
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}

.contact__option:hover {
    background-color: var(--color-dark);
    color: white;
}

.contact__option-icon {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.contact__option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}

.contact__option:hover a {
    color: white;
}

.contact__option a:hover {
    color: rgb(157, 205, 240);
}

form .btn-primary{
    width: 100%;
}

form {
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input,
textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: var(--color-bg-variant);
    border: 2px solid var(--color-primary-variant);
    resize: none;
}

@media screen and (max-width: 1024px) {
    .container.contact__container {
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}

@media screen and (max-width: 600px) {
    .container.contact__container {
        width: var(--container-width-sm);
    }
}
footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-bg);
}

.footer__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.footer__logo:hover {
    color: var(--color-bg-variant);
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.permalinks a:hover {
    color: var(--color-bg-variant);
}

.footer__socials {
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socials a {
    background: var(--color-bg);
    border-radius: 0.7rem;
    color: var(--color-primary);
    padding: 0.8rem;
    display: flex;
    border: 1px solid transparent;
}

.footer__socials a:hover {
    background: rgb(70, 70, 70);
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer__copyright {
    margin-bottom: 4rem;
    color: var(--color-bg);
}

@media screen and (max-width: 600px) {
    .permalinks {
        flex-direction: column;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }

    .footer__socials {
        margin-bottom: 2.6rem;
    }
}
